import React, { Component } from 'react';
import ParallaxComponent from 'react-parallax-component';
import './App.css';

class App extends Component {
  render() {
    return (
        <ParallaxComponent
            speed="0.003"
            width="300"
            top="40%"
            left="100"
        >
            <div>
                Children component
            </div>
        </ParallaxComponent>
    );
  }
}

export default App;



